import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'

import Themer from './AppThemer'
import CssBaseline from '@material-ui/core/CssBaseline'
import Header from './AppHeader'

import BottomNavigation from '@material-ui/core/BottomNavigation'
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import FolderIcon from '@material-ui/icons/Folder';
import HomeIcon from '@material-ui/icons/Home';
import FavoriteIcon from '@material-ui/icons/Favorite';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';

import Container from '@material-ui/core/Container'
import { createStyles, makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(() =>
  createStyles({
    root: {
      display: 'flex',
      flexDirection: 'column',
      minHeight: '93vh'
    },
    main: {
      flexGrow: 1,
      display: 'flex',
      alignItems: 'center'
    },
    rootB: {
    },
  })
)

export interface HomeLayoutProps {
  title: string
  children: any
}

const HomeLayout: React.SFC<HomeLayoutProps> = ({ title, children }) => {
  useEffect(() => {
    document.title = title
  })
  const history = useHistory()

  const classes = useStyles()
  const [value, setValue] = React.useState('home');

  const handleChange = (event: any, newValue: any) => {
    console.log("newValue", newValue);
    setValue(newValue);
    if(newValue == 'account'){
      history.push('/checkout')
    }else if(newValue == 'home'){
      history.push('/')
    }
  };

  return (
    <Themer>
      <div className={classes.root}>
      <CssBaseline />
        <Header />
        <Container className={classes.main}>{children}</Container>
      </div>

      <BottomNavigation value={value} onChange={handleChange} className={classes.rootB}>
      <BottomNavigationAction label="Home" value="home" icon={<HomeIcon />} />
      <BottomNavigationAction label="Favorites" value="favorites" icon={<FavoriteIcon />} />
      <BottomNavigationAction label="Account" value="account" icon={<AccountCircleIcon />} />
      <BottomNavigationAction label="Folder" value="folder" icon={<FolderIcon />} />
    </BottomNavigation>
    </Themer>
  )
}

export default HomeLayout
